@import url(https://fonts.googleapis.com/css2?family=Inter:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  text-decoration: none;
  color:inherit;
}
.container {
  width: 100%;
  display: flex;
  height: 100vh;
  font-weight: 400;
  font-family: Inter;
}

.login {
  justify-content: center;
}

.left {
  position: relative;
  height: 100%;
  font-size: 20px;
}

.login .left {
  margin-right: 100px;
  max-width: 580px;
  min-width:525px;
}
.left .logo {
  display: block;
  margin: 40px 0px;
}
.login .qoute-icon {
  margin-top: 3rem;
  display: inline-block;
}
.login .left .title {
  width: 450px;
  font-style: italic;
  margin-top: 8px;
}

.author {
  font-weight: 500;
  margin-top: 40px;
}

.login .left .controller {
  position: absolute;
  left: 280px;
  top: 380px;
  text-align: right;
  height: 300px;
}
.right {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login .right {
  max-width: 580px;
  min-width:525px;
}

.login .right .title {
  margin-top: 90px;
  font-size: 42px;
}

.login .right .subtitle {
  font-size: 18px;
  margin-top: -20px;
}
.right .identity-providers {
  display: flex;
  width: 300px;
  justify-content: space-evenly;
}

.identity-provider {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 65px;
  height: 64px;
  border-radius: 6px;
  box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 8%);
  cursor: pointer;
}

.identity-provider:hover {
  background-color: #eeeeee;
}

/* FORM  */

.formBlock {
  width: 400px;
  margin: auto;
  margin-top: 25px;
}
.check-block {
  width: 400px;
  margin: auto;
  margin-top: 10px;
}
.register .formBlock {
  margin-top: 15px;
}

input {
  border: 1px solid #8692a6;
  box-sizing: border-box;
  border-radius: 6px;
  font-size: 15px;
  width: 100%;
  height: 55px;
  -webkit-box-sizing: border-box;
  padding: 20px;
  display: block;
  margin-top: 8px;
}

input[type="password" i] {
  padding-right: 50px;
}
input[type="checkbox" i] {
  width: 20px;
  height: 20px;
  padding: 0px;
  margin: 0px;
  cursor: pointer;
}

.btn {
  width: 400px;
  background-color: #1565d8;
  border-radius: 6px;
  height: 55px;
  color: white;
  cursor: pointer;
  font-size: 16px;
  border: none;
}
.btn:hover {
  background-color: #01579b;
}

.line {
  width: 400px;
  border-bottom: 1px solid#E0E0E0;
}
.line-v2 {
  margin-top: 10px;
}

.label_inputs {
  font-size: 16px;
  color: black;
}

.login-form {
  margin-top: 15px;
  width: 100%;
}

.right .register {
  margin-top: 1rem;
  font-size: 16px;
}

.right .register span {
  color: #0277bd;
  cursor: pointer;
  font-weight: bold;
}

.right .register span:hover {
  color: #01579b;
}

.eye-icon {
  position: absolute;
  top: 0;
  right: 15px;
  margin: auto;
  bottom: 0;
  cursor: pointer;
}

/* Register page */
.register {
  height: 100vh;
}
.register .left {
  background-image: url(/react-forms/static/media/game.0796ab0a.png);
  background-repeat: no-repeat;
  background-position: center;
  padding-left: 50px;
  background-size: 100% 100%;
  width: 30%;
  max-width: 500px;
  min-width: 500px;
}

.register .left .logo {
}

.register .qoute-icon {
  margin-top: 60px;
}

.register .left .title {
  color: white;
  width: 88%;
  line-height: 38px;
  font-size: 20px;
  margin-top: 0px;
}

.register .author {
  color: white;
}

.group-icon {
  margin-left: 320px;
  display: block;
}

.downArrow-icon {
  margin-left: 380px;
  margin-top: 40px;
}

.register .right {
  position: relative;
  min-width: 750px;
  max-width: 750px;

}

.back-container {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 35px;
  left: 60px;
  cursor: pointer;
  width: 80px;
}

.back-container:hover {
  color: black !important;
}

.register-form {
  margin-top: 55px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.register-form .subtitle {
  margin-top: -10px;
  width: 70%;
  font-size: 20px;
}
.agreeBlock {
  display: flex;
  align-items: center;
}

.agreeBlock .label_inputs {
  margin-left: 8px;
}



.register input {
  height: 55px;
}

.register .btn {
  height: 55px;
}
.error_label {
  color: #f44336;
  display: block;
  font-size: 15px;
  margin-top: 2px;
}

.error {
  border: 1px solid #f44336 !important;
}
.google-btn {
  background: #ffffff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.08);
  color: black;
  width: 100%;
  border-radius: 6px;
  height: 55px;
  cursor: pointer;
  font-size: 16px;
  border: none;
  position: relative;
  margin-top: 5px;
  width: 400px;
}

.google-btn > img {
  height: 20px;
  position: absolute;
  left: 10px;
}

.google-btn:hover {
  background-color: #eeeeee;
}

/* REGISTER */
@media (max-width: 83rem) {
  .register .formBlock {
    margin-top: 10px ;
  }
  .register .btn {
    height: 45px;
  }
  .register .google-btn {
    height: 45px;
  }

  .register input {
    height: 45px;
  }

  
}

